"use client"

import * as React from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Logo } from "@/components/ui/logo"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Separator } from "@/components/ui/separator"
import { useState } from "react"
import { useRouter } from "next/navigation"

interface FormData {
  // Motor details
  nombre: string
  numeroSerie: string
  marca: string
  modelo: string
  potencia: string
  voltaje: string
  amperaje: string
  rpm: string
  tipo: string
  ubicacion: string
  
  // Maintenance details
  fechaMantenimiento: string
  tipoMantenimiento: string
  descripcion: string
  recomendaciones: string
  proximoMantenimiento: string
  
  // Personnel details
  tecnicoResponsable: string
  supervisor: string
  departamento: string
  
  // Images
  imagenesAntes: File[]
  imagenesDespues: File[]
}

export default function NuevoMotor() {
  const router = useRouter()
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [activeTab, setActiveTab] = useState("motor")
  
  const [formData, setFormData] = useState<FormData>({
    nombre: "",
    numeroSerie: "",
    marca: "",
    modelo: "",
    potencia: "",
    voltaje: "",
    amperaje: "",
    rpm: "",
    tipo: "",
    ubicacion: "",
    fechaMantenimiento: "",
    tipoMantenimiento: "",
    descripcion: "",
    recomendaciones: "",
    proximoMantenimiento: "",
    tecnicoResponsable: "",
    supervisor: "",
    departamento: "",
    imagenesAntes: [],
    imagenesDespues: []
  })

  const validateForm = () => {
    const errors = []
    
    // Motor validation
    if (!formData.nombre.trim()) errors.push('El nombre del motor es requerido')
    if (!formData.numeroSerie.trim()) errors.push('El n√∫mero de serie es requerido')
    if (!formData.marca.trim()) errors.push('La marca es requerida')
    if (!formData.modelo.trim()) errors.push('El modelo es requerido')
    
    // Maintenance validation
    if (!formData.fechaMantenimiento) errors.push('La fecha de mantenimiento es requerida')
    if (!formData.tipoMantenimiento.trim()) errors.push('El tipo de mantenimiento es requerido')
    if (!formData.descripcion.trim()) errors.push('La descripci√≥n es requerida')
    
    // Personnel validation
    if (!formData.tecnicoResponsable.trim()) errors.push('El t√©cnico responsable es requerido')
    if (!formData.supervisor.trim()) errors.push('El supervisor es requerido')
    
    // Images validation
    if (formData.imagenesAntes.length === 0) errors.push('Se requiere al menos una imagen antes del mantenimiento')
    if (formData.imagenesDespues.length === 0) errors.push('Se requiere al menos una imagen despu√©s del mantenimiento')

    if (errors.length > 0) {
      setError(errors.join('\n'))
      return false
    }
    return true
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError(null)
    
    if (!validateForm()) {
      return
    }

    setLoading(true)
    
    try {
      const formDataToSend = new FormData()
      
      // Append all form fields
      Object.entries(formData).forEach(([key, value]) => {
        if (key !== 'imagenesAntes' && key !== 'imagenesDespues') {
          formDataToSend.append(key, value.toString().trim())
        }
      })
      
      // Append images
      formData.imagenesAntes.forEach((file) => {
        formDataToSend.append('imagenesAntes', file)
      })
      
      formData.imagenesDespues.forEach((file) => {
        formDataToSend.append('imagenesDespues', file)
      })

      const response = await fetch('/api/motores', {
        method: 'POST',
        body: formDataToSend,
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || 'Error al guardar el motor')
      }

      router.push('/')
    } catch (error) {
      setError(error instanceof Error ? error.message : 'Error al guardar el motor')
    } finally {
      setLoading(false)
    }
  }

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>, type: "antes" | "despues") => {
    if (e.target.files && e.target.files.length > 0) {
      const files = Array.from(e.target.files)
      setFormData(prev => ({
        ...prev,
        [type === "antes" ? "imagenesAntes" : "imagenesDespues"]: files
      }))
    }
  }

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-50 to-white">
      <div className="container mx-auto">
        <div className="max-w-4xl mx-auto py-8 px-4">
          <Card className="border border-gray-200 bg-white/95 backdrop-blur-sm shadow-2xl hover:shadow-3xl transition-all duration-500">
          <CardHeader className="bg-[#0f172a] relative overflow-hidden py-12">
            {/* Animated gradient background */}
            <div className="absolute inset-0 bg-[radial-gradient(circle_at_30%_50%,rgba(255,255,255,0.1),transparent)] opacity-50"></div>
            <div className="absolute inset-0 bg-[linear-gradient(45deg,transparent_25%,rgba(255,255,255,0.05)_50%,transparent_75%)] bg-[length:20px_20px] animate-[gradient_3s_linear_infinite]"></div>
            
            {/* Decorative elements */}
            <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-blue-500 via-purple-500 to-blue-500"></div>
            <div className="absolute -left-4 top-1/2 -translate-y-1/2 w-12 h-12 bg-blue-500/20 rounded-full blur-xl"></div>
            <div className="absolute -right-4 top-1/2 -translate-y-1/2 w-12 h-12 bg-purple-500/20 rounded-full blur-xl"></div>
            
            {/* Grid pattern */}
            <div className="absolute inset-0 bg-[linear-gradient(rgba(255,255,255,0.05)_1px,transparent_1px),linear-gradient(90deg,rgba(255,255,255,0.05)_1px,transparent_1px)] bg-[size:20px_20px] [mask-image:radial-gradient(ellipse_at_center,transparent_20%,black_70%)]"></div>
            
            <CardTitle className="text-3xl font-bold text-white relative z-10 text-center tracking-tight">
              <span className="bg-clip-text text-transparent bg-gradient-to-r from-white via-gray-100 to-white">
                Registro de Mantenimiento de Motor
              </span>
            </CardTitle>
          </CardHeader>
          <CardContent className="p-6">
          <form onSubmit={handleSubmit} className="space-y-6" noValidate>
            {error && (
              <div className="bg-red-50 border-l-4 border-red-400 p-4 rounded-md mb-6">
                <div className="flex">
                  <div className="ml-3">
                    <h3 className="text-sm font-medium text-red-800">Por favor corrija los siguientes errores:</h3>
                    <div className="mt-2 text-sm text-red-700">
                      <ul className="list-disc pl-5 space-y-1">
                        {error.split('\n').map((err, index) => (
                          <li key={index}>{err}</li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            )}

            <Tabs value={activeTab} onValueChange={setActiveTab} className="space-y-4">
              <TabsList className="grid w-full grid-cols-4 bg-[#0f172a] rounded-xl p-1 shadow-lg relative overflow-hidden">
                {/* Grid pattern for TabsList */}
                <div className="absolute inset-0 bg-[linear-gradient(rgba(255,255,255,0.05)_1px,transparent_1px),linear-gradient(90deg,rgba(255,255,255,0.05)_1px,transparent_1px)] bg-[size:20px_20px] [mask-image:radial-gradient(ellipse_at_center,transparent_20%,black_70%)]"></div>
                
                <TabsTrigger 
                  value="motor" 
                  className="data-[state=active]:bg-white data-[state=active]:text-gray-900 data-[state=active]:shadow-md data-[state=active]:scale-[0.98] text-gray-300 hover:text-white transition-all duration-300 font-medium relative overflow-hidden group"
                >
                  <span className="relative z-10">Datos del Motor</span>
                  <div className="absolute inset-0 bg-gradient-to-r from-white/20 to-transparent opacity-0 group-hover:opacity-20 transition-opacity duration-300"></div>
                </TabsTrigger>
                <TabsTrigger 
                  value="maintenance" 
                  className="data-[state=active]:bg-white data-[state=active]:text-gray-900 data-[state=active]:shadow-md data-[state=active]:scale-[0.98] text-gray-300 hover:text-white transition-all duration-300 font-medium relative overflow-hidden group"
                >
                  <span className="relative z-10">Mantenimiento</span>
                  <div className="absolute inset-0 bg-gradient-to-r from-white/20 to-transparent opacity-0 group-hover:opacity-20 transition-opacity duration-300"></div>
                </TabsTrigger>
                <TabsTrigger 
                  value="personnel" 
                  className="data-[state=active]:bg-white data-[state=active]:text-gray-900 data-[state=active]:shadow-md data-[state=active]:scale-[0.98] text-gray-300 hover:text-white transition-all duration-300 font-medium relative overflow-hidden group"
                >
                  <span className="relative z-10">Personal</span>
                  <div className="absolute inset-0 bg-gradient-to-r from-white/20 to-transparent opacity-0 group-hover:opacity-20 transition-opacity duration-300"></div>
                </TabsTrigger>
                <TabsTrigger 
                  value="images" 
                  className="data-[state=active]:bg-white data-[state=active]:text-gray-900 data-[state=active]:shadow-md data-[state=active]:scale-[0.98] text-gray-300 hover:text-white transition-all duration-300 font-medium relative overflow-hidden group"
                >
                  <span className="relative z-10">Im√°genes</span>
                  <div className="absolute inset-0 bg-gradient-to-r from-white/20 to-transparent opacity-0 group-hover:opacity-20 transition-opacity duration-300"></div>
                </TabsTrigger>
              </TabsList>

              <TabsContent value="motor" className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="nombre" className="text-gray-700 font-medium">Nombre del Motor</Label>
                    <Input
                      id="nombre"
                      value={formData.nombre}
                      onChange={(e) => setFormData(prev => ({ ...prev, nombre: e.target.value }))}
                      placeholder="Ej: Motor Principal L√≠nea 1"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400 rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500/20 focus:shadow-md"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="numeroSerie" className="text-gray-700 font-medium">N√∫mero de Serie</Label>
                    <Input
                      id="numeroSerie"
                      value={formData.numeroSerie}
                      onChange={(e) => setFormData(prev => ({ ...prev, numeroSerie: e.target.value }))}
                      placeholder="Ej: SN2024001"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400 rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500/20 focus:shadow-md"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="marca" className="text-gray-700 font-medium">Marca</Label>
                    <Input
                      id="marca"
                      value={formData.marca}
                      onChange={(e) => setFormData(prev => ({ ...prev, marca: e.target.value }))}
                      placeholder="Ej: Siemens"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400 rounded-lg shadow-sm focus:ring-2 focus:ring-blue-500/20 focus:shadow-md"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="modelo" className="text-gray-700 font-medium">Modelo</Label>
                    <Input
                      id="modelo"
                      value={formData.modelo}
                      onChange={(e) => setFormData(prev => ({ ...prev, modelo: e.target.value }))}
                      placeholder="Ej: 1LA7"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="potencia" className="text-gray-700 font-medium">Potencia</Label>
                    <Input
                      id="potencia"
                      value={formData.potencia}
                      onChange={(e) => setFormData(prev => ({ ...prev, potencia: e.target.value }))}
                      placeholder="Ej: 75 HP"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="voltaje" className="text-gray-700 font-medium">Voltaje</Label>
                    <Input
                      id="voltaje"
                      value={formData.voltaje}
                      onChange={(e) => setFormData(prev => ({ ...prev, voltaje: e.target.value }))}
                      placeholder="Ej: 440V"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="amperaje" className="text-gray-700 font-medium">Amperaje</Label>
                    <Input
                      id="amperaje"
                      value={formData.amperaje}
                      onChange={(e) => setFormData(prev => ({ ...prev, amperaje: e.target.value }))}
                      placeholder="Ej: 150A"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="rpm" className="text-gray-700 font-medium">RPM</Label>
                    <Input
                      id="rpm"
                      value={formData.rpm}
                      onChange={(e) => setFormData(prev => ({ ...prev, rpm: e.target.value }))}
                      placeholder="Ej: 1750"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="tipo" className="text-gray-700 font-medium">Tipo de Motor</Label>
                    <Input
                      id="tipo"
                      value={formData.tipo}
                      onChange={(e) => setFormData(prev => ({ ...prev, tipo: e.target.value }))}
                      placeholder="Ej: Trif√°sico"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="ubicacion" className="text-gray-700 font-medium">Ubicaci√≥n</Label>
                    <Input
                      id="ubicacion"
                      value={formData.ubicacion}
                      onChange={(e) => setFormData(prev => ({ ...prev, ubicacion: e.target.value }))}
                      placeholder="Ej: Planta 1 - L√≠nea de Producci√≥n"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                </div>
              </TabsContent>

              <TabsContent value="maintenance" className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="fechaMantenimiento" className="text-gray-700 font-medium">Fecha de Mantenimiento</Label>
                    <Input
                      id="fechaMantenimiento"
                      type="date"
                      value={formData.fechaMantenimiento}
                      onChange={(e) => setFormData(prev => ({ ...prev, fechaMantenimiento: e.target.value }))}
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="tipoMantenimiento" className="text-gray-700 font-medium">Tipo de Mantenimiento</Label>
                    <Input
                      id="tipoMantenimiento"
                      value={formData.tipoMantenimiento}
                      onChange={(e) => setFormData(prev => ({ ...prev, tipoMantenimiento: e.target.value }))}
                      placeholder="Ej: Preventivo, Correctivo"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                  <div className="col-span-2 space-y-2">
                    <Label htmlFor="descripcion" className="text-gray-700 font-medium">Descripci√≥n del Mantenimiento</Label>
                    <Textarea
                      id="descripcion"
                      value={formData.descripcion}
                      onChange={(e) => setFormData(prev => ({ ...prev, descripcion: e.target.value }))}
                      placeholder="Detalle las actividades realizadas durante el mantenimiento"
                      rows={4}
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400 resize-none"
                    />
                  </div>
                  <div className="col-span-2 space-y-2">
                    <Label htmlFor="recomendaciones" className="text-gray-700 font-medium">Recomendaciones</Label>
                    <Textarea
                      id="recomendaciones"
                      value={formData.recomendaciones}
                      onChange={(e) => setFormData(prev => ({ ...prev, recomendaciones: e.target.value }))}
                      placeholder="Indique recomendaciones para futuros mantenimientos"
                      rows={3}
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400 resize-none"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="proximoMantenimiento" className="text-gray-700 font-medium">Pr√≥ximo Mantenimiento</Label>
                    <Input
                      id="proximoMantenimiento"
                      type="date"
                      value={formData.proximoMantenimiento}
                      onChange={(e) => setFormData(prev => ({ ...prev, proximoMantenimiento: e.target.value }))}
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300"
                    />
                  </div>
                </div>
              </TabsContent>

              <TabsContent value="personnel" className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="tecnicoResponsable" className="text-gray-700 font-medium">T√©cnico Responsable</Label>
                    <Input
                      id="tecnicoResponsable"
                      value={formData.tecnicoResponsable}
                      onChange={(e) => setFormData(prev => ({ ...prev, tecnicoResponsable: e.target.value }))}
                      placeholder="Nombre del t√©cnico"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="supervisor" className="text-gray-700 font-medium">Supervisor</Label>
                    <Input
                      id="supervisor"
                      value={formData.supervisor}
                      onChange={(e) => setFormData(prev => ({ ...prev, supervisor: e.target.value }))}
                      placeholder="Nombre del supervisor"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="departamento" className="text-gray-700 font-medium">Departamento</Label>
                    <Input
                      id="departamento"
                      value={formData.departamento}
                      onChange={(e) => setFormData(prev => ({ ...prev, departamento: e.target.value }))}
                      placeholder="Ej: Mantenimiento Industrial"
                      className="bg-white/50 hover:bg-white focus:bg-white border-gray-200 hover:border-gray-300 focus:border-gray-400 transition-all duration-300 placeholder:text-gray-400"
                    />
                  </div>
                </div>
              </TabsContent>

              <TabsContent value="images" className="space-y-6">
                <div className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="imagenesAntes" className="text-gray-700 font-medium">Im√°genes Antes del Mantenimiento</Label>
                    <Input
                      id="imagenesAntes"
                      type="file"
                      accept="image/*"
                      multiple
                      onChange={(e) => handleImageChange(e, "antes")}
                      className="border-2 border-dashed p-4 bg-white/50 hover:bg-white focus:bg-white border-gray-300 hover:border-gray-400 focus:border-gray-500 transition-all duration-300 cursor-pointer file:bg-gray-900 file:text-white file:border-0 file:rounded-md file:px-4 file:py-2 file:mr-4 file:hover:bg-gray-800 file:transition-colors"
                    />
                    {formData.imagenesAntes.length > 0 && (
                      <p className="text-sm text-gray-500 mt-2">
                        {formData.imagenesAntes.length} im√°genes seleccionadas
                      </p>
                    )}
                  </div>

                  <Separator className="my-6" />

                  <div className="space-y-2">
                    <Label htmlFor="imagenesDespues" className="text-gray-700 font-medium">Im√°genes Despu√©s del Mantenimiento</Label>
                    <Input
                      id="imagenesDespues"
                      type="file"
                      accept="image/*"
                      multiple
                      onChange={(e) => handleImageChange(e, "despues")}
                      className="border-2 border-dashed p-4 bg-white/50 hover:bg-white focus:bg-white border-gray-300 hover:border-gray-400 focus:border-gray-500 transition-all duration-300 cursor-pointer file:bg-gray-900 file:text-white file:border-0 file:rounded-md file:px-4 file:py-2 file:mr-4 file:hover:bg-gray-800 file:transition-colors"
                    />
                    {formData.imagenesDespues.length > 0 && (
                      <p className="text-sm text-gray-500 mt-2">
                        {formData.imagenesDespues.length} im√°genes seleccionadas
                      </p>
                    )}
                  </div>
                </div>
              </TabsContent>
            </Tabs>

            <div className="flex justify-end space-x-4 pt-6 border-t">
              <Button 
                variant="outline" 
                type="button" 
                onClick={() => router.push("/")}
                disabled={loading}
                className="border-gray-300 hover:bg-gray-100/50 hover:text-gray-900 transition-all duration-300"
              >
                Cancelar
              </Button>
              <Button 
                type="submit"
                disabled={loading}
                className="min-w-[120px] bg-gradient-to-r from-gray-900 via-gray-800 to-gray-900 hover:from-gray-800 hover:via-gray-700 hover:to-gray-800 text-white shadow-lg hover:shadow-xl transition-all duration-300"
              >
                {loading ? (
                  <div className="flex items-center justify-center">
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2"></div>
                    <span>Guardando...</span>
                  </div>
                ) : (
                  "Guardar Motor"
                )}
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
          <footer className="mt-16 text-center border-t border-gray-200 pt-12 pb-8">
            <div className="max-w-md mx-auto">
              <div className="py-4 px-8">
                <p className="text-base text-gray-600 mb-6 font-medium tracking-wide">
                  Desarrollado por
                </p>
                <div className="opacity-80 hover:opacity-100 transition-opacity">
                  <Logo />
                </div>
              </div>
            </div>
          </footer>
        </div>
      </div>
    </main>
  )
}
